<template>
  <div id="app" style="display: block">
    <router-view />
  </div>
</template>

<script>
export default {
  name: "App",

  mounted() {
    let v = navigator.userAgent.split("/")[2];
    if (v < 5 || !v) {
      this.$alert(
        `您的软件版本过低，请更新到最新版使用！<br>https://wws.lanzous.com/i0F9ojmr19i<br>请点击确认按钮复制下载链接到浏览器中下载！`,
        `提示`,
        {
          dangerouslyUseHTMLString: true,
          showConfirmButton: true,
          showCancelButton: true,
          closeOnClickModal: false,
          lockScroll: false,
          callback: (action) => {
            if (action === "confirm") {
              this.$copyText(`https://wws.lanzous.com/i0F9ojmr19i`).then(
                (e) => {
                  this.$message({
                    type: "success",
                    message: "已复制到剪切板!",
                  });
                  document.querySelector("#app").style.display = "none";
                }
              );
            } else {
              document.querySelector("#app").style.display = "none";
            }
          },
        }
      );
    }
  },
};
</script>

<style>
@import url("./styles/reset.css");
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  text-align: center;
}
</style>



