<template>
  <div class="htmleaf-container">
    <div class="wrapper">
      <div class="container">
        <h1>Classmate-Sun's Cloud Login</h1>
        <div class="formBox">
          <el-form :model="form" :rules="rules">
            <el-form-item label="用户名" prop="username">
              <el-input
                placeholder="请输入内容"
                v-model="form.username"
                clearable
                @keyup.enter.native="signIn"
              ></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input
                placeholder="请输入密码"
                v-model="form.password"
                clearable
                show-password
                @keyup.enter.native="signIn"
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="success" plain @click="signIn">登录</el-button>
              <el-button plain @click="resetForm('ruleForm')">清空</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>

      <ul class="bg-bubbles">
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "index",
  data() {
    return {
      form: {
        username: "",
        password: "",
      },
      rules: {
        //用户名验证
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            //数据类型
            type: "string",
            //正则规则
            pattern: /^[\w\u4e00-\u9fa5\uac00-\ud7ff\u0800-\u4e00\-]{2,7}$/,
            message: "请输入正确格式用户名",
            trigger: ["blur", "change"],
          },
        ],

        //密码验证
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            type: "string",
            pattern: /^[\w<>,.?|;':"{}!@#$%^&*()\/\-\[\]\\]{6,18}$/,
            message: "请输入正确格式用户名",
            trigger: ["blur", "change"],
          },
        ],
      },
      hostUrl: "",
    };
  },
  methods: {
    // 登录
    signIn() {
      this.dialogFormVisible = false;
      this.$http
        .post(`${this.hostUrl}/user/validate`, this.form)
        .then((res) => {
          if (res.data.flag === 0) {
            this.$message.error(res.data.msg);
          } else {
            this.$message.success(res.data.msg);
            sessionStorage.setItem("uid", res.data.uid);
            sessionStorage.setItem("username", res.data.username);

            //  跳转到其他页面
            this.$router.push("tab-list");
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //  注册
    // signUp () {
    //   this.dialogFormVisible = false
    //   this.$http
    //     .post('/user/add', this.form)
    //     .then(res => {
    //       if (res.data.flag === 0) {
    //         this.$message.error(res.data.msg)
    //       } else {
    //         this.$message.success(res.data.msg)
    //         this.$router.push('tab-list')
    //       }
    //     })
    //     .catch(err => {
    //       console.log(err)
    //     })
    // }
  },
  mounted() {
    let protocol = window.location.protocol; //协议
    let host = window.location.host; //主机
    this.hostUrl = `${protocol}//${host}`;
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.wrapper {
  background: #50a3a2;
  background: -webkit-linear-gradient(top left, #50a3a2 0%, #53e3a6 100%);
  background: linear-gradient(to bottom right, #50a3a2 0%, #53e3a6 100%);
  opacity: 0.8;
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 400px;
  margin-top: -200px;
  overflow: hidden;
}
.container {
  position: relative;
  box-sizing: border-box;
  max-width: 600px;
  margin: 0 auto;
  padding: 63px 0;
  height: 400px;
  text-align: center;
  z-index: 99999;
}
.formBox{
  margin: 0 auto;
  width: 300px;
}
.container h1 {
  font-size: 40px;
  transition-duration: 1s;
  transition-timing-function: ease-in-put;
  font-weight: 200;
  color: #fff;
}
.container .el-form .el-form-item {
  margin-bottom: 0px;
}
.container .el-form .el-form-item .el-form-item__content {
  margin-top: 30px;
}
.container .el-form .el-form-item .el-form-item__label {
  color: #fff;
}
.container .el-form .el-form-item .el-button + .el-button {
  margin-left: 40px;
  margin-top: 40px;
}
.bg-bubbles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 400px;
  z-index: 1;
}
.bg-bubbles li {
  position: absolute;
  list-style: none;
  display: block;
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.2);
  bottom: -160px;
  animation: square 15s infinite;
  transition-timing-function: linear;
  border-radius: 40%;
}
.bg-bubbles li:nth-child(1) {
  left: 10%;
}
.bg-bubbles li:nth-child(2) {
  left: 20%;
  width: 80px;
  height: 80px;
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
  -webkit-animation-duration: 17s;
  animation-duration: 17s;
}
.bg-bubbles li:nth-child(3) {
  left: 25%;
  -webkit-animation-delay: 4s;
  animation-delay: 4s;
}
.bg-bubbles li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  -webkit-animation-duration: 22s;
  animation-duration: 22s;
  background-color: rgba(255, 255, 255, 0.25);
}
.bg-bubbles li:nth-child(5) {
  left: 70%;
}
.bg-bubbles li:nth-child(6) {
  left: 80%;
  width: 120px;
  height: 120px;
  -webkit-animation-delay: 3s;
  animation-delay: 3s;
  background-color: rgba(255, 255, 255, 0.2);
}
.bg-bubbles li:nth-child(7) {
  left: 32%;
  width: 160px;
  height: 160px;
  -webkit-animation-delay: 7s;
  animation-delay: 7s;
}
.bg-bubbles li:nth-child(8) {
  left: 55%;
  width: 20px;
  height: 20px;
  -webkit-animation-delay: 15s;
  animation-delay: 15s;
  -webkit-animation-duration: 40s;
  animation-duration: 40s;
}
.bg-bubbles li:nth-child(9) {
  left: 25%;
  width: 10px;
  height: 10px;
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
  -webkit-animation-duration: 40s;
  animation-duration: 40s;
  background-color: rgba(255, 255, 255, 0.3);
}
.bg-bubbles li:nth-child(10) {
  left: 90%;
  width: 160px;
  height: 160px;
  -webkit-animation-delay: 11s;
  animation-delay: 11s;
}
@-webkit-keyframes square {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-700px) rotate(600deg);
    transform: translateY(-700px) rotate(600deg);
  }
}
@keyframes square {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    -webkit-transform: translateY(-700px) rotate(600deg);
    transform: translateY(-700px) rotate(600deg);
  }
}
</style>


